.form {
	background-color: #fff;
	&__field {
		display: block;
		width: 100%;
		background-color: transparent;
		font-size: 14px;
		text-transform: uppercase;
		color: #a7a7a7;
		text-align: center;
		margin-bottom: 23px;
		padding: 15px 10px;
		&:focus {
			&::placeholder {
				color: #000;
			}
		}
		&::placeholder {
			color: #939494;
		}
		&.error {
			border-color: red;
		}
	}
	&__textarea {
		height: 100%;
	}
	&__submit {
		width: 100%;
		padding: 19px 10px;
		max-width: 290px;
		margin: 0 auto;
	}
}