/* ==========================================================================
   CENTER
========================================================================== */
@mixin center() {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

/* ==========================================================================
   MEDIA
========================================================================== */
@mixin e($width) {
	@media only screen and (max-width: $width+ "px") {
		@content;
	}
}

/* ==========================================================================
   FONT
========================================================================== */
@mixin font($name, $fontName) {
	@font-face {
		font-family: $name;
	    src: url("../fonts/"+$fontName+".woff2") format("woff2"),
	         url("../fonts/"+$fontName+".woff") format("woff");
	    font-style: normal;
	    font-weight: normal;
	    font-display: swap;
	}
}