/* ==========================================================================
   FRONT
========================================================================== */
.front {
  background-color: #0a0f2c;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  padding: 270px 0 80px;
  color: #fff;
  position: relative;
  @include sm {
    padding-top: 140px;
  }
  &__inner {
    display: flex;
    align-items: center;
    @include md {
      display: block;
    }
  }
  &__left {
    max-width: 630px;
    @include md {
      max-width: 100%;
      text-align: center;
    }
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    @include md {
      margin-top: 30px;
      justify-content: center;
    }
  }
  &__title {
    font-size: 70px;
    line-height: 1;
    margin-bottom: 20px;
    @include xs {
      font-size: 38px;
    }
    strong {
      font-family: $bold;
    }
    span {
      font-family: $thin;
    }
  }
  &__desc {
    font-size: 20px;
    line-height: 1.2;
    margin-bottom: 35px;
    @include xs {
      font-size: 16px;
    }
  }
}

.join-form {
  width: 370px;
  background-color: rgba(#000, 0.4);
  padding: 40px;
  @include xs {
    width: 100%;
    padding: 40px 10px;
  }
  &__title {
    text-align: center;
    font-size: 30px;
    line-height: 1;
    font-family: $bold;
  }
  &__desc {
    text-align: center;
    font-size: 20px;
    position: relative;
    margin-bottom: 60px;
    &:after {
      content: "";
      position: absolute;
      width: 270px;
      height: 1px;
      background-color: $accent;
      left: 50%;
      transform: translateX(-50%);
      bottom: -25px;
    }
  }
  &__wrap {
    width: 250px;
    margin: 0 auto 25px auto;
    label {
      display: block;
      font-family: $medium;
      line-height: 1;
      margin-bottom: 20px;
    }
    input {
      display: block;
      border-radius: 35px;
      background-color: #fff;
      font-size: 15px;
      font-family: $medium;
      width: 100%;
      height: 55px;
      padding: 0 20px;
      color: $text;
      &::placeholder {
        color: #d1d1d1;
      }
    }
  }
  &__policy {
    text-align: center;
    font-size: 14px;
    line-height: 1;
    font-family: $light;
    margin-bottom: 20px;
  }
  &__button {
    display: block;
    width: 100%;
    background-color: #ef1552;
    box-shadow: 0px 3px 27px 0px rgba(239, 21, 82, 0.66);
    height: 75px;
    border-radius: 35px;
    font-size: 20px;
    line-height: 1;
    font-family: $medium;
    text-align: center;
    &:hover {
      background-color: lighten(#ef1552, 10);
    }
  }
}

.front-page {
  padding-bottom: 210px;
  position: relative;
  @include xs {
    padding-bottom: 120px;
  }
  &:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 43px;
    background-image: url(../images/front-page-bottom.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @include md {
      background-size: cover;
      bottom: -1px;
    }
  }
  &__inner {
    text-align: center;
  }
  &__title {
    font-size: 70px;
    line-height: 1;
    font-family: $bold;
    margin-bottom: 30px;
    @include xs {
      font-size: 38px;
    }
  }
  &__desc {
    font-size: 20px;
    line-height: 1.2;
    @include sm {
      font-size: 16px;
      br {
        display: none;
      }
    }
  }
}

.scroll-down {
  position: absolute;
  bottom: 80px;
  left: 100px;
  @include e(1600) {
    left: 10px;
  }
  @include lg {
    display: none;
  }
}

.rates {
  img {
    width: 100%;
  }
}

/* ==========================================================================
   PRICES
========================================================================== */
.prices {
  padding: 100px 0;
  &__top {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    border-bottom: 1px solid #dedede;
    margin-bottom: 40px;
    @include md {
      display: block;
    }
  }
  &__crypto {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: -1px;
    @include md {
      margin-bottom: 20px;
    }
    @include xs {
      justify-content: center;
    }
    a {
      display: flex;
      align-items: center;
      margin-right: 25px;
      border-bottom: 3px solid transparent;
      padding-bottom: 10px;
      @include xs {
        margin: 10px;
      }
      &:hover {
        border-color: $accent;
      }
      &.active {
        border-color: $accent;
      }
      &:last-child {
        margin-right: 0;
      }
      img {
        display: block;
        margin-right: 10px;
      }
      span {
        font-size: 16px;
        line-height: 1;
        font-family: $medium;
      }
    }
  }
  &__time {
    position: relative;
    margin-bottom: -1px;
    display: flex;
    align-items: center;
    @include md {
      justify-content: flex-end;
    }
    @include xs {
      justify-content: center;
    }
    a {
      display: block;
      margin-right: 15px;
      border-bottom: 3px solid transparent;
      padding-bottom: 10px;
      font-size: 16px;
      line-height: 1;
      font-family: $medium;
      &:hover {
        border-color: $accent;
      }
      &.active {
        border-color: $accent;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__left,
  &__middle,
  &__right {
    position: relative;
    @include sm {
      margin-bottom: 20px;
    }
  }
  &__info {
    border-bottom: 1px solid #dedede;
    margin-bottom: 60px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 30px;
    @include sm {
      display: block;
    }
  }
  &__count {
    font-size: 50px;
    line-height: 1;
    font-family: $gil;
    display: flex;
    align-items: center;
    sup {
      font-size: 20px;
      position: relative;
      top: -20px;
    }
    @include sm {
      justify-content: center;
    }
  }
  &__name {
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    display: block;
    line-height: 1;
    font-family: $medium;
    margin-top: 15px;
    color: #929292;
  }
  &__direction {
    position: absolute;
    left: -50px;
    top: 10px;
  }
}

/* ==========================================================================
   PRICING
========================================================================== */
.middle-pricing {
  text-align: center;
  display: flex;
  justify-content: center;
}
.pricing {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    display: flex;
    text-align: center;
    @include lg {
      display: block;
    }
  }
  &__left {
    min-width: 400px;
    max-width: 800px;
    padding-bottom: 77px;
    @include lg {
      min-width: 100%;
      max-width: 100%;
      padding-bottom: 40px;
      padding-right: 0;
    }
    span {
      font-family: $light;
      color: #888a8b;
      display: block;
      margin-bottom: 15px;
    }
    h3 {
      font-size: 50px;
      line-height: 1;
      font-family: $bold;
      margin-bottom: 5px;
      text-transform: uppercase;
      @include xs {
        font-size: 38px;
      }
    }
    p {
      font-size: 18px;
      color: #888a8b;
    }
  }
  &__right {
    flex-grow: 1;
    display: flex;
    margin: -30px 0;
    @include lg {
      margin: 0;
      justify-content: center;
    }
    @include sm {
      display: block;
    }
  }
  &__item {
    width: 370px;
    text-align: center;
    padding: 70px 30px 40px 30px;
    color: #fff;
    @include sm {
      width: 100%;
    }
    span {
      display: block;
      font-family: $bold;
      line-height: 1;
      margin-bottom: 35px;
    }
    strong {
      display: block;
      font-size: 29px;
      line-height: 1;
      margin-bottom: 45px;
      font-family: $rob;
      color: $text;
    }
    p {
      line-height: 2.1;
    }
    &:first-child {
      background-color: #4a86fe;
    }
    &:nth-child(2) {
      margin-left: 30px;
      background-color: $accent;
    }
    &:last-child {
      background-color: #4262c1;
      margin-left: 30px;
      @include sm {
        margin-left: 0;
        margin-top: 20px;
      }
    }
  }
  &__button {
    margin-top: 60px;
    width: 100%;
  }
  .__hover {
    transition: 0.25s;
    &:hover {
      background-color: #4262c1;
    }
  }
}
@media screen and (max-width: 768px) {
  .pricing__item:nth-child(2) {
    margin-left: 0;
    margin-top: 20px;
  }
}

//CALCULATOR
.calculator-section {
  margin-top: 75px;
  background-color: #43c1a8;
  padding-top: 53px;
  .calculator-inner {
    display: flex;
    justify-content: space-between;
    .div-text {
      p {
        color: #ffffff;
        font-family: Roboto - Black;
        font-size: 60px;
        font-weight: 400;
        line-height: 50px;
        text-transform: uppercase;
      }
    }
    .div-middle {
      text-align: center;
      .calc-main {
        position: relative;
        display: flex;
        align-items: center;
        input {
          position: relative;
          padding-left: 60px;
          max-width: 270px;
          height: 124px;
          border: 1px solid #b4b4b4;
          border-radius: 2px;
          color: #363636;
          font-family: Roboto-Black;
          font-size: 72px;
          font-weight: 400;
          background-color: #ffffff;
          border-right: 0px;
        }
        .calc-select-currency {
          #input-p {
            font-size: 72px;
          }
          cursor: pointer;
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100px;
          height: 124px;
          border: 1px solid #b4b4b4;
          background-color: #ffffff;
          border-left: 0px;
          color: #363636;
          font-family: Roboto-Black;
          font-size: 72px;
          font-weight: 400;
          padding-right: 150px;
          p {
            margin-left: 40px;
          }
          &::after {
            content: ">";
            width: 22px;
            height: 15px;
            position: absolute;
            top: 40%;
            right: 0;
            font-family: Roboto-Black;
            font-weight: 400;
            transform: rotate(90deg);
            font-size: 72px;
          }
          .dropdown-block {
            display: none;
            opacity: 0;
            transition: 0.5s;
            z-index: 1000;
            position: absolute;
            width: 110%;
            top: 100%;
            right: 0;
            flex-direction: column;
            justify-content: center;
            border: 1px solid #b4b4b4;
            background-color: #ffffff;
            button {
              transition: 0.25s;
              padding-left: 10px;
              &:hover {
                background-color: rgb(226, 226, 226);
              }
            }
          }
          .active {
            display: flex;
            opacity: 1;
          }
        }
      }
    }
    .div-end {
      display: flex;
      justify-content: end;
      flex-direction: column;
      .first-select {
        .input-p {
          &::after {
            content: "";
            width: 9px;
            height: 7px;
            background-image: url("../../images/_downarrow1.png");
            right: 0;
            bottom: 0;
          }
        }
        &::after {
          content: "";
          width: 9px;
          height: 7px;
          position: absolute;
          background-image: url("../../images/_downarrow1.png");
          right: 18px;
        }
      }
    }
    .second-select {
      position: relative;
      &::after {
        content: "";
        width: 51px;
        height: 24px;
        position: absolute;
        background-image: url("../../images/symbol1.png");
        right: 18px;
      }
    }
    .___select {
      transition: 0.5s;
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;
      margin-bottom: 13px;
      position: relative;
      padding-left: 18px;
      width: 370px;
      height: 47px;
      border: 1px solid #b4b4b4;
      border-radius: 2px;
      color: #363636;
      font-family: Roboto-Black;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      background-color: #ffffff;
      .dropdown-block {
        display: none;
        opacity: 0;
        transition: 0.5s;
        z-index: 1000;
        position: absolute;
        transition: 0.25s;
        width: 100%;
        top: 100%;
        right: 0;
        flex-direction: column;
        justify-content: center;
        border: 1px solid #b4b4b4;
        background-color: #ffffff;
        button {
          transition: 0.25s;
          padding-left: 18px;
          height: 47px;
          &:hover {
            background-color: rgb(226, 226, 226);
          }
        }
      }
      .active {
        display: flex;
        opacity: 1;
      }
    }
    .last-block {
      height: 47px;
      float: left;
      background-color: #fff;
      padding-left: 18px;
      padding-right: 18px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
        color: #363636;
        font-family: Roboto-Black;
        font-size: 14px;
        font-weight: 500;
        line-height: 20px;
        &:last-child {
          font-size: 24px;
        }
      }
    }
  }
}
@media screen and (max-width: 1200px) {
  .calculator-section .calculator-inner {
    flex-direction: column;
    justify-content: center;
    .div-text {
      display: flex;
      justify-content: center;
    }
    .div-middle {
      display: flex;
      justify-content: center;
      flex-direction: column;
      .calc-main {
        margin-left: auto;
        margin-right: auto;
        margin-top: 50px;
      }
      .button--calc {
        margin-left: auto;
        margin-right: auto;
      }
    }
    .div-end{
      display: flex;
      justify-content: center;
      .first-select{
        margin-left: auto;
        margin-right: auto;
      }
      .second-select{
        margin-left: auto;
        margin-right: auto;
      }
      .last-block{
        margin-left: auto;
        margin-right: auto;
        width: 370px;
        margin-bottom: 50px;
      }
    }
  }
}
@media screen and (max-width: 768px){
  .calculator-section .calculator-inner .div-middle .calc-main input{
    padding-left: 30px;
    max-width: 240px;
  }
}
/* ==========================================================================
   FEATURES
========================================================================== */
.features {
  position: relative;
  padding: 110px 0 80px;
  &:before {
    content: "";
    position: absolute;
    @include center();
    z-index: -1;
    width: 570px;
    height: 570px;
    border-radius: 50%;
    border: 1px solid #f6f6f6;
  }
  &:after {
    content: "";
    position: absolute;
    @include center();
    z-index: -1;
    width: 1060px;
    height: 1060px;
    border-radius: 50%;
    border: 1px solid #f6f6f6;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 33.3%;
    margin-bottom: 80px;
    text-align: center;
    padding: 0 8px;
    @include lg {
      width: 50%;
    }
    @include sm {
      width: 100%;
      margin-bottom: 40px;
    }
    strong {
      display: block;
      margin: 20px 0 10px;
      font-size: 30px;
      line-height: 1;
      font-family: $bold;
    }
    p {
      font-size: 20px;
      color: #888a8b;
    }
  }
  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 65px;
  }
}

/* ==========================================================================
   TRUST
========================================================================== */
.trust {
  background-color: #040936;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  padding: 80px 0 100px;
  text-align: center;
  color: #fff;
  &__number {
    font-size: 180px;
    line-height: 1;
    font-family: $rob;
    @include md {
      font-size: 120px;
    }
    @include xs {
      font-size: 63px;
    }
  }
  &__desc {
    font-size: 25px;
    line-height: 1;
    font-family: $medium;
    text-transform: uppercase;
    margin-bottom: 60px;
    margin-top: 35px;
    @include xs {
      font-size: 20px;
    }
  }
}

/* ==========================================================================
   FAQ
========================================================================== */
.faq {
  padding: 100px 0 80px;
  &__item {
    border-top: 1px solid #e3e3e3;
  }
  &__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 0;
    &.active {
      .faq__icon {
        border-color: $accent;
        .icon-plus {
          display: none;
        }
        .icon-cancel {
          display: block;
        }
      }
    }
  }
  &__name {
    display: block;
    font-size: 20px;
    line-height: 1;
    font-family: $bold;
    @include xs {
      margin-right: 10px;
      font-size: 16px;
    }
  }
  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 2px solid #497bfd;
    .icon {
      font-size: 12px;
    }
    .icon-plus {
      fill: #497bfd;
    }
    .icon-cancel {
      display: none;
      fill: $accent;
    }
  }
  &__desc {
    font-size: 20px;
    line-height: 1.5;
    color: #888a8b;
    display: none;
  }
  &__more {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 35px;
    border: 2px solid $accent;
    font-family: $medium;
    text-align: center;
    line-height: 1;
    height: 56px;
    width: 184px;
    margin: 50px auto 0 auto;
    &:hover {
      background-color: $accent;
      color: #fff;
    }
  }
}

/* ==========================================================================
   START
========================================================================== */
.start {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  padding: 95px 0;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    @include md {
      display: block;
      text-align: center;
    }
  }
  &__title {
    color: #fff;
    font-size: 40px;
    line-height: 1;
    font-family: $bold;
    text-transform: uppercase;
    margin-right: 60px;
    @include md {
      margin-right: 0;
      margin-bottom: 20px;
    }
    @include xs {
      font-size: 32px;
    }
  }
}

/* ==========================================================================
   UPDATES
========================================================================== */
.updates {
  background-color: #fafbfb;
  padding: 50px;
  @include xs {
    padding: 50px 10px;
  }
  &__item {
    padding-bottom: 40px;
    display: flex;
    justify-content: center;
  }
  &__link {
    display: block;
    width: 370px;
    padding: 50px 35px 70px 35px;
    background-color: #fff;
    @include xs {
      padding: 50px 15px 70px 15px;
    }
    &:hover {
      box-shadow: 0px 29px 18px 0px rgba(154, 154, 154, 0.26);
    }
  }
  &__top {
    display: flex;
    align-items: center;
    color: $accent;
  }
  &__date {
    display: block;
    font-size: 80px;
    line-height: 1;
    font-family: $rob;
    margin-right: 20px;
  }
  &__month {
    display: block;
    font-size: 20px;
    line-height: 1;
    margin-bottom: 5px;
    font-family: $bold;
  }
  &__year {
    display: block;
    font-size: 20px;
    line-height: 1;
    font-family: $bold;
  }
  &__name {
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 25px;
    line-height: 1;
    font-family: $bold;
  }
  &__desc {
    font-size: 20px;
    color: #888a8b;
  }
  &__dots {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
    @include xs {
      margin-top: 30px;
    }
    .owl-dot {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #dfdfdf;
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        background-color: #d4d4d4;
      }
      &.active {
        width: 12px;
        height: 12px;
        background-color: #d4d4d4;
      }
    }
  }
}

/* ==========================================================================
   FAQ-PAGE
========================================================================== */
.faq-page {
  padding: 140px 0 230px;
  @include sm {
    padding: 80px 0;
  }
}

.search {
  width: 770px;
  border-radius: 35px;
  background-color: #222427;
  margin: 0 auto 140px auto;
  display: flex;
  align-items: center;
  padding: 7px;
  @include md {
    width: 100%;
  }
  @include xs {
    display: block;
    margin-bottom: 60px;
  }
  &__input {
    display: block;
    flex-grow: 1;
    background-color: #fff;
    border-radius: 35px;
    height: 55px;
    padding: 20px;
    color: #555555;
    line-height: 30px;
    @include xs {
      width: 100%;
    }
  }
  &__button {
    margin-left: 5px;
    padding: 19px 47.5px;
    @include xs {
      width: 100%;
      margin-left: 0;
    }
  }
}

/* ==========================================================================
   INFO-BLOCK
========================================================================== */
.info-block {
  padding: 90px 0;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    @include md {
      display: block;
      text-align: center;
    }
  }
  &__title {
    font-size: 40px;
    line-height: 1;
    font-family: $bold;
    margin-right: 80px;
    @include md {
      margin-right: 0;
      margin-bottom: 20px;
    }
    @include xs {
      font-size: 32px;
    }
  }
}

/* ==========================================================================
   FEES-PAGE
========================================================================== */
.fees-page {
  padding: 90px 0 160px;
  &__table {
    @include xs {
      overflow: scroll;
    }
    table {
      min-width: 460px;
      display: block;
      width: 100%;
      border: 1px solid #eceff2;
      background-color: #fff;
      tbody {
        display: block;
        width: 100%;
        tr {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          padding: 9px 30px;
          border-bottom: 1px solid #eceff2;
