              font-size: 16px;
              font-family: $medium;
            }
          }
          &.fees-page__gray {
            background-color: #eceff2;
          }
          &:nth-child(even) {
            background-color: #fbfbfb;
          }
          &:last-child {
            border-bottom: none;
          }
          th {
            font-size: 14px;
            font-family: $medium;
          }
          td {
            font-size: 14px;
            line-height: 1;
            min-width: 120px;
            text-align: center;
            &:first-child {
              text-align: left;
            }
          }
        }
      }
