    padding: 80px 0;
  }
  &__inner {
    display: flex;
    @include lg {
      flex-wrap: wrap;
    }
  }
  &__item {
    width: 370px;
    margin: 15px;
    @include lg {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &:last-child {
      margin-right: 0;
    }
