      margin-top: 30px;
    }
  }
}

.login-form {
  width: 370px;
  border: 1px solid #ededed;
  padding: 60px 30px;
  @include xs {
    width: 100%;
    padding: 40px 15px;
  }
  &__title {
    text-align: center;
    font-size: 30px;
    line-height: 1;
    font-family: $bold;
    margin-bottom: 30px;
  }
  &__desc {
    color: #888a8b;
    font-size: 16px;
    margin-bottom: 20px;
    @include xs {
      br {
        display: none;
      }
    }
  }
  &__input {
    display: block;
    width: 100%;
    border-radius: 35px;
    border: 1px solid #e0e0e0;
    padding: 0 20px;
    font-size: 16px;
    line-height: 27px;
    height: 55px;
    margin-bottom: 20px;
    &--password {
      background-image: url(../images/eye.png);
