  &__button {
    width: 100%;
    margin-top: 40px;
  }
  &__bottom {
    margin-top: 35px;
    text-align: center;
    font-size: 16px;
    line-height: 1;
    color: #888a8b;
    a {
      color: $accent;
      &:hover {
        text-decoration: underline;
      }
