      justify-content: flex-start;
    }
  }
  &__item {
    width: 33.3%;
    @include lg {
      width: 50%;
    }
    @include sm {
      width: 100%;
    }
  }
  &__link {
    display: block;
    background-color: #fff;
    width: 370px;
    margin: 0 auto 30px auto;
    @include xs {
      width: 100%;
    }
