  &__tag {
    display: block;
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 1;
    font-family: $bold;
    color: $accent;
  }
  &__name {
    font-size: 25px;
    line-height: 1.2;
    font-family: $bold;
    margin-bottom: 20px;
  }
  &__desc {
    font-size: 20px;
    color: #888a8b;
  }
  &__date {
    display: block;
    font-family: $bold;
    font-size: 16px;
    line-height: 1;
    margin-top: 20px;
  }
  &__more {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 35px;
    border: 2px solid $accent;
    font-family: $medium;
    text-align: center;
    line-height: 1;
    height: 56px;
    width: 184px;
    margin: 50px auto 0 auto;
    &:hover {
      background-color: $accent;
      color: #fff;
    }
