/* ==========================================================================
   NEWS-SINGLE
========================================================================== */
.news-single {
  padding: 120px 0;
  &__inner {
    width: 770px;
    margin: 0 auto;
    @include md {
      width: 100%;
    }
  }
  &__image {
    display: block;
    margin: 0 auto;
  }
  &__info {
    padding: 40px;
    @include xs {
      padding: 40px 0;
    }
  }
  &__tag {
    color: $accent;
    line-height: 1;
    font-family: $bold;
    display: block;
    margin-bottom: 15px;
  }
  &__name {
    font-family: $bold;
    font-size: 25px;
    margin-bottom: 10px;
  }
  &__date {
    display: block;
    line-height: 1;
    font-family: $bold;
    margin-bottom: 20px;
  }
  &__desc {
    p {
      font-size: 20px;
      color: #888a8b;
      margin: 20px 0;
      @include xs {
        font-size: 16px;
      }
    }
    blockquote {
      display: block;
      background-color: $accent;
      margin: 30px -40px;
      padding: 40px 40px 40px 50px;
      color: #fff;
      font-size: 25px;
      @include xs {
        font-size: 18px;
      }
    }
  }
}

.socials {
  display: flex;
  align-items: center;
  a {
    display: block;
    margin-right: 12px;
    &:last-child {
      margin-right: 0;
    }
  }
}

/* ==========================================================================
   REFERAL
========================================================================== */
.referal-page {
  padding: 330px 0;
  @include lg {
    padding: 100px 0 300px 0;
  }
  &__inner {
    display: flex;
    align-items: center;
    @include lg {
      display: block;
    }
  }
  &__left {
    min-width: 460px;
    max-width: 460px;
    @include lg {
      min-width: 100%;
      max-width: 100%;
    }
  }
  &__right {
    margin-left: auto;
    @include lg {
      margin-top: 180px;
    }
  }
  &__top {
    text-transform: uppercase;
    display: block;
    line-height: 1;
    color: #888a8b;
    font-family: $light;
    margin-bottom: 15px;
  }
  &__title {
    font-size: 40px;
    line-height: 1;
    font-family: $bold;
    margin-bottom: 20px;
    @include xs {
      font-size: 32px;
    }
  }
  &__desc {
    font-size: 20px;
    color: #888a8b;
    @include xs {
      font-size: 16px;
    }
  }
  &__list {
    margin-top: 60px;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 35px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 70px;
    max-width: 70px;
    margin-right: 15px;
    img {
      display: block;
    }
  }
  &__info {
    strong {
      display: block;
      font-size: 22px;
      line-height: 1;
      font-family: $bold;
      text-transform: uppercase;
      margin-bottom: 5px;
      @include xs {
        font-size: 18px;
      }
    }
    p {
      font-size: 18px;
      line-height: 1.2;
      color: #888a8b;
      @include xs {
        font-size: 14px;
      }
    }
  }
  &__link {
    display: block;
    margin: 50px auto 0 auto;
    background-color: #9adafd;
    text-transform: uppercase;
    text-align: center;
    padding: 14px 0px;
    max-width: 186px;
    color: #fff;
    border-radius: 35px;
    &:hover {
      background-color: darken(#9adafd, 10);
    }
  }
  &__wrap {
    text-align: center;
    color: #fff;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      width: 828px;
      height: 849px;
      background-image: url(../images/referal-image.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      @include center();
      z-index: -1;
    }
    p {
      font-size: 30px;
      line-height: 1.2;
      font-family: $light;
      @include xs {
        br {
          display: none;
        }
        font-size: 24px;
      }
    }
    strong {
      font-size: 150px;
      line-height: 1;
      font-family: $bold;
    }
  }
}

/* ==========================================================================
   PAGE
========================================================================== */
.page {
  padding: 140px 0;
  @include xs {
    padding: 70px 0;
  }
  &__inner {
    h2 {
      font-size: 30px;
      line-height: 1.4;
      font-family: $bold;
    }
    p {
      font-size: 20px;
      color: #888a8b;
      margin: 15px 0;
      @include xs {
        font-size: 16px;
      }
      a {
        color: $accent;
        text-decoration: underline;
      }
    }
    hr {
      border: 0.5px solid #e9e9e9;
      margin: 70px 0;
      @include xs {
        margin: 30px 0;
      }
    }
    .dropcaps {
      display: flex;
      @include sm {
        display: block;
      }
      p {
        width: 50%;
        @include sm {
          width: 100%;
        }
        &:nth-child(odd) {
          margin-right: 30px;
          @include sm {
            margin-right: 0;
          }
          &:first-letter {
            font-size: 60px;
            line-height: 0;
            color: $accent;
            font-family: $bold;
          }
        }
      }
    }
    blockquote {
      display: block;
      font-size: 25px;
      padding-left: 40px;
      border-left: 5px solid $accent;
      margin: 20px 0px;
      @include xs {
        font-size: 18px;
        padding-left: 20px;
        border-left-width: 2px;
      }
    }
    ol {
      list-style-type: decimal;
      padding-left: 15px;
      margin: 10px 0;
      li {
        font-size: 20px;
        color: #888a8b;
      }
    }
    ul {
      margin: 10px 0;
      li {
        position: relative;
        padding-left: 15px;
        font-size: 20px;
        color: #888a8b;
        &:after {
          content: "";
          position: absolute;
          width: 7px;
          height: 2px;
          background-color: #888a8b;
          left: 0;
          top: 50%;
          margin-top: -1px;
        }
      }
    }
  }
}
.__block-text {
  background-color: #43c1a8;
  padding-top: 5%;
  padding-bottom: 5%;
  padding-left: 5%;
  padding-right: 5%;
  margin-top: 5%;
  p {
    min-height: 136px;
    color: #ffffff;
    font-family: Catamaran-Light;
    font-size: 25px;
    font-weight: 400;
    line-height: 37px;
    margin: 0px;
  }
}
.end-lists {
  display: flex;
  justify-content: space-between;
  &-div {
    max-width: 348px;
  }
}
/* ==========================================================================
   TRUST-PAGE
========================================================================== */
.trust-page {
  padding: 90px 0;
  &__top {
    display: flex;
    margin-bottom: 80px;
