      }
    }
    @include sm {
      margin: 15px;
    }
    @include xs {
      width: 100%;
      margin: 15px 0;
      padding: 50px 15px 40px 15px;
    }
    &--blue {
      border-color: #4262c1;
      .trust-page__button {
        background-color: #4262c1;
        &:hover {
          background-color: lighten(#4262c1, 10);
        }
      }
      .trust-page__price {
        color: #4262c1;
      }
    }
    &--violet {
      border-color: #694e96;
      .trust-page__button {
        background-color: #694e96;
        &:hover {
          background-color: lighten(#694e96, 10);
        }
      }
      .trust-page__price {
        color: #694e96;
      }
    }
    ul {
      padding-left: 45px;
      @include xs {
        padding-left: 0;
      }
      li {
        position: relative;
        padding-left: 30px;
        font-size: 16px;
        color: #888a8b;
        &:before {
          content: "";
          position: absolute;
          width: 15px;
          height: 11px;
          left: 0;
          top: 8px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          background-image: url("../images/t-c.png");
        }
        &.false {
          &:before {
            background-image: url("../images/t-f.png");
