    line-height: 1;
    font-family: $medium;
    background-color: $accent;
    border-radius: 35px;
    margin-top: 60px;
    &:hover {
      background-color: lighten($accent, 10);
    }
  }
  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
    margin-bottom: 15px;
  }
  &__name {
    display: block;
    text-align: center;
    font-size: 25px;
    line-height: 1;
    font-family: $bold;
    margin-bottom: 40px;
  }
  &__price {
    text-align: center;
    display: block;
    font-size: 50px;
    line-height: 1;
    font-family: $rob;
    margin-bottom: 30px;
    color: $accent;
    span {
      font-size: 20px;
      font-family: $regular;
    }
  }
}

