.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 45px 65px 0 65px;
  &__inner {
    display: flex;
    justify-content: space-between;
    @include lg {
      align-items: center;
    }
  }
  @include e(1400) {
    padding: 40px 20px 0 20px;
  }
  @include xs {
    padding-top: 15px;
  }
}

.logo {
  display: flex;
  align-items: center;
  &__image {
    display: block;
  }
  &__text {
    display: flex;
    align-items: center;
    font-size: 26px;
    line-height: 1;
    padding-left: 12px;
    strong {
      font-family: $bold;
    }
    span {
      font-family: $light;
    }
  }
}

.humburger {
  display: none;
  z-index: 10;
  @include lg {
    display: block;
  }
}

.menu {
  display: flex;
  align-items: center;
  @include lg {
    display: none;
    position: absolute;
    top: 100px;
    right: 15px;
    background-color: #fff;
    padding: 20px 20px 20px 60px;
    border-radius: 10px;
    z-index: 10;
  }
  li {
    margin-right: 40px;
    @include lg {
      margin-right: 0;
      margin-bottom: 10px;
      text-align: right;
    }
    &:last-child {
      margin-right: 0;
    }
    a {
      display: block;
      font-size: 18px;
      line-height: 1;
      padding-bottom: 4px;
      border-bottom: 2px solid transparent;
      @include lg {
        color: #000;
        border-bottom: none;
        padding-bottom: 0;
      }
      &:hover {
        border-color: #fff;
        @include lg {
          text-decoration: underline;
        }
      }
      &.active {
        border-color: #fff;
      }
    }
  }
}

.user-navigation {
  display: flex;
  align-items: center;
  @include xs {
    display: none;
  }
  &__login {
    display: block;
    font-size: 14px;
    line-height: 1;
    background-color: #10cb6c;
    border-radius: 35px;
    padding: 12px 37px;
    margin-right: 15px;
    &:hover {
      background-color: lighten(#10cb6c, 10);
    }
  }
  &__sign {
    display: block;
    font-size: 14px;
    line-height: 1;
    &:hover {
      text-decoration: underline;
    }
  }
  &__languages {
    margin-left: 35px;
    position: relative;
    &:hover {
      .user-navigation__list {
        display: block;
      }
    }
  }
  &__selected {
    a {
      display: block;
      width: 33px;
      height: 33px;
      border-radius: 50%;
      border: 4px solid #2a2d3a;
      &:hover {
        border-color: $accent;
      }
    }
  }
  &__list {
    position: absolute;
    top: 30px;
    right: 0;
    display: none;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33px;
      height: 33px;
      border-radius: 50%;
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
.rates {
  overflow: hidden;
}
.rates-div {
  transform: translateX(-100px);
  width: 4500px;
  height: 116px;
  overflow: hidden;
  background-color: #030c17;
  display: flex;
  .item-first {
    display: flex;
    align-items: center;
    h1 {
      color: #ffffff;
      font-family: "Lato - Black";
      font-size: 12px;
      font-weight: 400;
      text-transform: uppercase;
      .span-green {
        color: #00ce4d;
        font-family: Lato;
        font-size: 11px;
        font-weight: 700;
        text-transform: uppercase;
      }
      .span-red {
        color: #ff651c;
        font-family: Lato;
        font-size: 11px;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
    .first-p {
      color: #ffffff;
      font-family: Lato;
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;
      span {
        color: #798086;
        font-family: Lato;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
      }
    }
    .second-p {
      color: #798086;
      font-family: Lato;
      font-size: 11px;
      font-weight: 500;
      span {
        color: #ffffff;
        font-family: Lato;
        font-size: 11px;
        font-weight: 500;
      }
    }
    img {
      width: 60px;
      height: 28px;
      margin-left: 5px;
      margin-right: 42px;
    }
  }
}
