.popup {
	display: none;
	position: relative;
	overflow: visible;
	width: 100%;
	padding: 35px 30px;
	@include xs {
		padding: 35px 15px;
	}
	&__close {
		position: absolute;
		right: 0px;
		top: 0px;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		height: 40px;
		background-color: #dcdcdc;
		z-index: 1;
		.icon {
			font-size: 26px;
		}
	}
}

/* ==========================================================================
   SENT
========================================================================== */
.sent {
	max-width: 400px;
	&__name {
		font-size: 24px;
		text-transform: uppercase;
	}
}

/* ==========================================================================
   RESET STYLES
========================================================================== */
.fancybox-slide--iframe .fancybox-content {
	max-width: 80%;
	@include xs {
		min-width: 96%;
		max-width: 96%;
	}
}
.fancybox-slide--iframe {
	padding: 40px 10px 10px 10px;
}
.fancybox-slide--html .fancybox-content {
	margin-bottom: 0;
}
.fancybox-close-small {
	display: none;
}
.fancybox-slide {
	cursor: pointer;
}