.policy {
	input {
		display: none;
		&:checked + label {
			&:after {
				background-color: $accent;
				border-color: $accent;
			}
		}
	}
	label {
		display: block;
		position: relative;
		font-size: 16px;
		line-height: 1;
		color: #888a8b;
		padding-left: 30px;
		cursor: pointer;
		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -8px;
			width: 15px;
			height: 15px;
			border: 1px solid #e3e3e3;
			border-radius: 1px;
		}
		a {
			color: $accent;
			&:hover {
				text-decoration: underline;
			}
		}
	}
}