.humburger {
    width: 35px;
    height: 24px;
    position: relative;
    cursor: pointer;
    display: inline-block;
    background: transparent;
    border: none;
    outline: none;
}
.humburger span {
    background-color: #fff;
    position: absolute;
    border-radius: 2px;
    transition: .3s cubic-bezier(.8, .5, .2, 1.4);
    width: 100%;
    height: 4px;
    transition-duration: 500ms;
}
.humburger span:nth-child(1) {
    top: 0px;
    left: 0px;
}
.humburger span:nth-child(2) {
    top: 10px;
    left: 0px;
}
.humburger span:nth-child(3) {
    bottom: 0px;
    left: 0px;
}
.humburger:not(.humburger-active):hover span:nth-child(1) {
    transform: rotate(-3deg) scaleY(1.1);
}
.humburger:not(.humburger-active):hover span:nth-child(2) {
    transform: rotate(3deg) scaleY(1.1);
}
.humburger:not(.humburger-active):hover span:nth-child(3) {
    transform: rotate(-4deg) scaleY(1.1);
}
.humburger-active span:nth-child(1) {
    transform: rotate(45deg);
    top: 10px;
}
.humburger-active span:nth-child(2) {
    transform: scale(.1);
}
.humburger-active span:nth-child(3) {
    transform: rotate(-45deg);
    top: 10px;
}