/* ==========================================================================
   BASE STYLES
   ========================================================================== */
body {
    position: relative;
    font-size: 18px;
    line-height: 1.6;
    font-family: $bodyFont;
    color: $bodyFontColor;
    background-color: $bodyBackgroundColor;
}

.wrapper {
    position: relative;
    overflow: hidden;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
    min-width: 320px;
}

/* ==========================================================================
   HELPER CLASSES
   ========================================================================== */
@keyframes fade {
    from {opacity: 0}
    to {opacity: 1}
}

.icon {
    width: 1em;
    height: 1em;
    fill: initial;
}

.hidden {
    display: none !important;
}

.visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    white-space: nowrap;
}