.button {
  display: inline-block;
  text-align: center;
  font-size: 16px;
  line-height: 1;
  font-family: $medium;
  padding: 20px 77px;
  border-radius: 35px;
  &--accent {
    background-color: $accent;
    color: #fff;
    &:hover {
      background-color: lighten($accent, 10);
    }
  }
  &--white {
    background-color: #fff;
    color: $text;
  }
  &--calc {
    border-radius: 21px;
    text-align: center;
    width: 310px;
    height: 56px;
    background-color: #363636;
    color: #ffffff;
    font-family: Catamaran-Medium;
    font-size: 16px;
    font-weight: 500;
    line-height: 40px;
    letter-spacing: 0.8px;
    margin-top: 32px;
    margin-bottom: 60px;
    transition: 0.25s;
    &:hover {
      background-color: lighten(#363636, 10);
    }
  }
}
