.footer {
	background-color: #292b30;
	padding: 30px 0;
	color: #fff;
	.logo {
		justify-content: center;
		color: #fff;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 45px;
		@include md {
			justify-content: flex-start;
		}
	}
	&__item {
		@include md {
			margin: 10px;
		}
		strong {
			color: #497bfd;
			font-size: 18px;
			line-height: 1;
			font-family: $bold;
			text-transform: uppercase;
			display: block;
			margin-bottom: 10px;
		}
		ul {
			li {
				a {
					display: block;
					font-size: 18px;
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
	&__bottom {
		margin-top: 60px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include xs {
			display: block;
		}
	}
	.copyright {
		color: #5a5d63;
		font-size: 13px;
		line-height: 1;
		@include xs {
			margin-bottom: 10px;
		}
	}
	&__nav {
		display: flex;
		align-items: center;
		font-size: 13px;
		line-height: 1;
		color: #5a5d63;
		a {
			text-decoration: underline;
			&:hover {
				color: #fff;
			}
		}
		span {
			display: block;
			margin: 0 3px;
		}
	}
}